import { createContext, useEffect, useState } from "react";

const AuthContext = createContext({
    isLoggedIn:false,
    //logouttu bul jerge jazbay srazu Appke jazsak bolot
    
})

export const AuthContextProvider = (props) => {
    const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    const storedUserLoggedInInformation = localStorage.getItem("isLoggedIn");

    if (storedUserLoggedInInformation === "1") {
      setIsLoggedIn(true);
    }
  }, []);

  const loginHandler = (email, password) => {
    // We should of course check email and password
    // But it's just a dummy/ demo anyways
    localStorage.setItem("isLoggedIn", "1");
    setIsLoggedIn(true);
  };

  const logoutHandler = () => {
    localStorage.removeItem("isLoggedIn");
    setIsLoggedIn(false);
  };
//MainHedaerge uje keregi jok potomu chto isLoggedIn globalniy bolgonu uchun =?useContexttin jardamy menen  

    return<AuthContext.Provider value={{isLoggedIn:isLoggedIn,onLogout:logoutHandler,onLogin:loginHandler}}>
        {props.children}

    </AuthContext.Provider>

}

export default AuthContext